"use client";

import { useEffect, useState } from "react";

/**
 * Hook pour d√©terminer si le code s'ex√©cute c√¥t√© client
 * √âvite les mismatches d'hydratation en garantissant la coh√©rence serveur/client
 */
export function useIsClient() {
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  return isClient;
}